
    import { defineComponent, ref } from 'vue'

    export default defineComponent({
        name: 'Modal',
        setup (props) {
            const isVisible = ref<boolean>(false)

            const open = () => {
                isVisible.value = true
            }

            const close = () => {
                isVisible.value = false
            }

            return {
                open,
                close,
                isVisible
            }
        }
    })
