<template>
    <div class="relative">
        <div ref="sliderRef" class="slider flex overflow-auto -mx-5 md:-mx-8 pt-2 pb-10" :class="{'multiple-items': itemCount && itemCount > 1}">
            <transition name="appear-from-left">
                <div class="arrow arrow-left" v-if="showLeftArrow" @click="onClickArrow('left')">
                    <img class="arrow-img" src="data:image/svg+xml;base64," />
                </div>
            </transition>
            <slot></slot>
            <transition name="appear-from-right">
                <div class="arrow arrow-right" v-if="showRightArrow" @click="onClickArrow('right')">
                    <img class="arrow-img" src="data:image/svg+xml;base64," />
                </div>
            </transition>
        </div>
    </div>
</template>

<script lang="ts">
    import { defineComponent, ref, onMounted, onUnmounted } from 'vue'

    const SCROLL_WIDTH = 400

    export default defineComponent({
        name: 'Slider',
        props: {
            itemCount: { default: null }
        },
        setup (props) {
            const sliderRef = ref<HTMLDivElement | null>(null)
            const scrollPercentage = ref<number>(1)
            const showLeftArrow = ref<boolean>(false)
            const showRightArrow = ref<boolean>(false)

            onMounted(() => {
                if (sliderRef?.value) {
                    sliderRef.value.addEventListener('scroll', handleScroll)
                    window.addEventListener('resize', () => handleScroll())
                    handleScroll()
                }
            })

            onUnmounted(() => {
                if (sliderRef?.value) {
                    sliderRef.value.removeEventListener('scroll', handleScroll)
                    window.removeEventListener('resize', () => handleScroll())
                }
            })

            const handleScroll = (e: any = null) => {
                if (!e && sliderRef?.value) {
                    e = {
                        target: sliderRef?.value
                    }
                }

                const { scrollLeft, scrollWidth, offsetWidth } = e.target

                scrollPercentage.value = scrollLeft
                    ? scrollLeft / (scrollWidth - offsetWidth)
                    : 0

                showLeftArrow.value = scrollPercentage.value > 0

                if (scrollWidth === offsetWidth || scrollPercentage.value === 1) {
                    showRightArrow.value = false
                } else {
                    showRightArrow.value = true
                }

                console.log({ scrollLeft, scrollWidth, offsetWidth, sliderRef: sliderRef.value })
                console.log({ showLeftArrow: showLeftArrow.value, showRightArrow: showRightArrow.value  })
            }

            const onClickArrow = (direction: 'left' | 'right') => {
                if (!sliderRef?.value) {
                    return null
                }

                if (direction === 'left') {
                    // sliderRef.value.scrollLeft -= 200
                    sliderRef.value.scrollTo({ left: sliderRef.value.scrollLeft - SCROLL_WIDTH })
                } else if (direction === 'right') {
                    sliderRef.value.scrollTo({ left: sliderRef.value.scrollLeft + SCROLL_WIDTH })
                }
            }

            return {
                sliderRef,
                showLeftArrow,
                showRightArrow,
                onClickArrow,
            }

        }
    })
</script>

<style lang="scss" scoped>
    .slider {
        scroll-snap-type: x proximity;
    }

    .multiple-items {
        &::after {
            content: '';
            @apply pr-5;
        }
    }

    .arrow {
        --size: 50px;

        scroll-behavior: smooth;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background: rgba(255, 255, 255, .9);
        backdrop-filter: blur(2px);
        height: var(--size);
        width: var(--size);
        border-radius: var(--size);
        z-index: 1000;
        box-shadow: 0 2px 10px 1px rgba(0, 0, 0, 0.1);
        cursor: pointer;
    }

    .arrow-img {
        height: calc(var(--size, 30px) / 3);
    }

    .arrow-left {
        left: 0;
    }

    .arrow-right {
        right: 0;
    }

    /* TRANSITION */
    .appear-from-left-enter-active, .appear-from-left-leave-active {
        transition: opacity .2s, left .2s;
        transition-timing-function: ease-in-out;
    }
    .appear-from-left-enter-from, .appear-from-left-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0!important;
        left: -20px!important;
    }

    .appear-from-right-enter-active, .appear-from-right-leave-active {
        transition: opacity .2s, right .2s;
        transition-timing-function: ease-in-out;
    }

    .appear-from-right-enter-from, .appear-from-right-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
        right: -20px;
    }
</style>
